/* Base */
body {
  font-family: monospace; }

ul {
  padding: 0;
  list-style: none; }

.fontWhite .menu__link,
.fontWhite .foot__link,
.fontWhite p,
.fontWhite h2 {
  color: white !important; }

.noSelect * {
  -webkit-user-select: none;
  /* Safari */
  -moz-user-select: none;
  /* Firefox */
  -ms-user-select: none;
  /* IE10+/Edge */
  user-select: none;
  /* Standard */ }

.invisible {
  visibility: hidden; }

iframe {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  width: 100%;
  height: 100%; }

.blur {
  transition: filter 0.3s ease-in-out; }
  .blur:hover {
    filter: blur(5px); }

@font-face {
  font-family: "Poppins-SemiBold";
  src: url("http://127.0.0.1:5500/dist/assets/media/fonts/Poppins/PoppinsLatin-SemiBold.ttf") format("truetype"); }

@font-face {
  font-family: "Poppins-Regular";
  src: url("http://127.0.0.1:5500/dist/assets/media/fonts/Poppins/PoppinsLatin-Regular.ttf") format("truetype"); }

@font-face {
  font-family: "Worksans-Bold";
  src: url("http://127.0.0.1:5500/dist/assets/media/fonts/Worksans/WorkSans-Bold.ttf") format("truetype"); }

@font-face {
  font-family: "Worksans-SemiBold";
  src: url("http://127.0.0.1:5500/dist/assets/media/fonts/Worksans/WorkSans-SemiBold.ttf") format("truetype"); }

@font-face {
  font-family: "Worksans-Medium";
  src: url("http://127.0.0.1:5500/dist/assets/media/fonts/Worksans/WorkSans-Medium.ttf") format("truetype"); }

@font-face {
  font-family: "Worksans-Regular";
  src: url("http://127.0.0.1:5500/dist/assets/media/fonts/Worksans/WorkSans-Regular.ttf") format("truetype"); }

/* Modules */
.menu {
  position: absolute;
  width: 250px;
  top: 50%;
  left: 30px;
  transform: translateY(-50%); }
  .menu__right {
    position: absolute;
    right: 0;
    transform: rotate(90deg); }
  .menu nav {
    margin-left: 10px;
    text-transform: uppercase; }
  .menu__title {
    font-family: "Poppins-SemiBold", Sans-Serif;
    font-size: 2.4rem;
    margin-top: 0; }
  .menu a {
    color: black;
    text-decoration: none;
    transition: all 0.3s ease-in-out; }
    .menu a:hover {
      color: red !important; }
  .menu__project {
    font-family: "Worksans-Regular", Sans-Serif;
    margin: 12px 0; }
    .menu__project a {
      font-size: 1rem;
      transition: all 0.3s ease-in-out; }
      .menu__project a:hover {
        letter-spacing: 1px;
        color: red !important; }
  .menu__separator {
    float: left;
    width: 200px; }
  .menu__refresh {
    display: inline-block;
    margin: 70px 0 0 70px; }
    .menu__refresh img {
      width: 30px;
      transition: all 0.3s ease-in-out; }
      .menu__refresh img:hover {
        transform: rotate(180deg); }

.foot {
  font-family: "Worksans-SemiBold", Sans-Serif;
  width: 500px;
  position: absolute;
  transform: rotate(90deg);
  top: 50%;
  right: -200px;
  text-align: center; }
  .foot a {
    font-size: 1rem;
    text-decoration: none;
    color: black;
    letter-spacing: 20px;
    transition: all 0.3s ease-in-out; }
    .foot a:hover {
      color: red !important;
      font-size: 2.4rem; }

.project {
  margin: 0 23% 0 32%;
  transform: translateY(-50%);
  position: absolute;
  top: 45%; }
  .project__title {
    font-size: 2.4rem;
    margin-bottom: 0; }
  .project__subtitle {
    font-size: 1.3rem;
    margin-top: 0;
    font-weight: 300; }
    .project__subtitle--dark {
      color: #7e7d7d !important; }
    .project__subtitle--light {
      color: silver !important; }
  .project__description {
    font-size: 1rem;
    margin-bottom: 3rem; }
  .project__section {
    position: relative;
    overflow: hidden;
    height: 0px;
    transition: height 0.5s ease-in-out; }
    .project__section--is-visible {
      height: 300px;
      display: block; }
  .project--squared {
    margin: 0 40% 0 38%; }

.btn {
  color: #f8384c;
  border-color: #f8384c;
  border: solid 3px;
  background: transparent;
  border-radius: 0px;
  text-decoration: none;
  padding: 10px 10px 10px 12px;
  font-size: 12px;
  line-height: 19px;
  text-transform: uppercase;
  letter-spacing: 4px;
  transition: all 0.4s ease-in-out;
  margin-bottom: 8px;
  margin-right: 4px;
  cursor: pointer; }
  .btn:hover {
    color: white;
    border-color: #fa132a;
    background-color: #fa132a; }
  .btn--is-highlighted {
    color: white !important;
    border-color: #fa132a !important;
    background-color: #fa132a !important; }

.gif {
  width: 333px;
  height: 204px;
  margin: 5% 0;
  background: url("../media/imgs/ssb.png") left;
  -webkit-animation: play 15s steps(40) infinite;
  -moz-animation: play 15s steps(40) infinite;
  -ms-animation: play 15s steps(40) infinite;
  -o-animation: play 15s steps(40) infinite;
  animation: play 15s steps(40) infinite; }

@keyframes play {
  100% {
    background-position: -14500px; } }

@-webkit-keyframes play {
  100% {
    background-position: -14500px; } }

.gif:hover {
  width: 333px;
  height: 204px;
  margin: 5% 0;
  background: url("../media/imgs/ssb.png") left;
  -webkit-animation: play 15s steps(40) infinite;
  -moz-animation: play 15s steps(40) infinite;
  -ms-animation: play 15s steps(40) infinite;
  -o-animation: play 15s steps(40) infinite;
  animation: play 15s steps(40) infinite; }

.gif:not(:hover) {
  -webkit-animation-play-state: paused;
  -moz-animation-play-state: paused;
  -ms-animation-play-state: paused;
  -o-animation-play-state: paused;
  animation-play-state: paused; }

.orbit {
  position: absolute;
  margin: 0 24% 0 40%;
  top: 32%;
  width: 400px;
  height: 400px;
  transform: rotate(0deg) translateY(-50%);
  transition: transform 0.7s infinite;
  animation: spinParent 50s infinite linear; }

.planet {
  position: absolute;
  font-size: 1rem;
  transform: rotate(0deg);
  transition: transform 0.7s infinite;
  animation: spinChild 50s infinite linear; }

@keyframes spinParent {
  0% {
    transform: rotate(0deg); }
  100% {
    transform: rotate(360deg); } }

@keyframes spinChild {
  0% {
    transform: rotate(0deg); }
  100% {
    transform: rotate(-360deg); } }
